.imagecontainer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;

  pointer-events: none;
}

.imagemenu {
  pointer-events: none;
  opacity: 0;
  position: absolute;
  right: calc(var(--bordersizing) * 1.2);
  top: 13vh;
  width: calc(var(--bordersizing) * 2.3);
  height: calc(var(--bordersizing) * 2);
  z-index: 3; /* Set a higher z-index to appear on top of .box */
  transition: opacity ease-in 0.2s;
}
.box {
  z-index: -2;
  transition: opacity ease-in 0.2s;
  height: var(--bordersizing);
  width: var(--bordersizing);
  position: absolute;
  box-shadow: calc(var(--bordersizing) + 7px) 7px 0 7px
      theme("colors.primarycolor"),
    calc(-1 * var(--bordersizing) - 7px) -7px 0 7px theme("colors.primarycolor");
  right: 26vw;
  top: 31.5vh;
  opacity: 0;

  transition: 0.2s 0.18s;
  pointer-events: none;
}
.box:before,
.box:after {
  content: "";
  position: absolute;
  height: 7px;
  width: 0%;
  background: theme("colors.primarycolor");
  transition: 0.2s;
}
.box:before {
  top: 7px;
  right: 7px;
}
.box:after {
  bottom: 7px;
  left: 7px;
}
.trigger:hover + .imagecontainer .box {
  opacity: 1;
  box-shadow: calc(var(--bordersizing) + 7px) calc(var(--bordersizing) + 14px) 0
      7px theme("colors.primarycolor"),
    calc(-1 * var(--bordersizing) - 7px) calc(-1 * var(--bordersizing) - 14px) 0
      7px theme("colors.primarycolor"),
    0 0 0 7px theme("colors.secondary2color");
  transition: box-shadow 0.2s;
}

.trigger:hover + .imagecontainer .box:after,
.trigger:hover + .imagecontainer .box:before {
  width: calc(var(--bordersizing) + 7px); /*change to 0 for normal borders */
  transition: width 0.2s 0.18s;
}
.box1 {
  z-index: -2;
  transition: opacity ease-in 0.2s;
  height: var(--bordersizing);
  width: var(--bordersizing);
