html {
  font-size: 0.5vw;
}

:root {
  --bordersizing: calc(18vw);
  --color-primary: #75757c;
  --color-secondary: theme("colors.secondary2color");
  --heading-text: 3.5vh;
}
body {
  font-size: 1.6rem;

  overflow-x: hidden;
  font-family: "Montserrat", sans-serif;
  color: white;
  background: linear-gradient(
    to right,
    theme("colors.secondary2color") 0%,
    theme("colors.secondary2color") 18%,
    theme("colors.primarycolor") 18%,
    theme("colors.primarycolor") 82%,
    theme("colors.secondary2color") 82%,
    theme("colors.secondary2color") 100%
  );
}

.main-container {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
}

.headline {
  font-family: "Archivo Black", sans-serif;
  letter-spacing: 1;
  text-transform: uppercase;
  font-weight: 600;
  color: #ffc900;
  margin: 0;
  margin-top: -7%;
  padding: 0;
  font-size: 12.5rem;
  text-shadow: 10px 10px 2px #7b7b7b,
    -10px -10px 2px theme("colors.secondary2color"),
    -10px 10px 2px theme("colors.secondary2color");
  line-height: 1.2;
}
.subhead {
  font-size: 2.4rem;
  font-weight: 300;
  margin-top: 1.5%;
  margin-bottom: 1.5%;
}

.highlight {
  font-style: italic;
  font-weight: bold;
  padding: 0rem 2rem;
  width: 70%;
}
.highlight-1 {
  color: theme("colors.secondary2color");
  background: grey;
  box-shadow: 10px 10px 2px #ffc900;
}
.highlight-2 {
  color: theme("colors.primarycolor");
  background: ffc90;
  box-shadow: 10px 10px 2px theme("colors.secondary2color");
}
.highlight-3 {
  color: #ffc900;
  background: grey;
  box-shadow: 10px 10px 2px theme("colors.secondary2color");
}
.article-meta {
  max-width: 200rem;
  width: 100%;
  display: flex;
  flex-direction: column;

  align-items: flex-start;
  justify-content: space-between;
}
.author {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}

.author svg {
  height: 3rem;
  width: 3rem;
  margin: 0 1rem 0 0;
}
.author p {
  margin-right: 1rem;
  color: gray;
  font-style: bold;
}

.article {
  font-size: 1.8rem;
  line-height: 2;
}
.article-para-1 {
  font-size: 2rem;
  line-height: 1.5;
  color: gray;
  margin-top: 2rem;
  margin-bottom: 1.5%;
}
.article-row {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
}
.article-col {
  flex: 1;
}
aside {
  font-size: 2.4rem;
  font-weight: bold;
  line-height: 1.2;
}
blockquote {
  text-indent: 0.5rem;
  box-shadow: -20px 0px 0px #ffc900;
}
.bq-long {
  border-left: 10px solid theme("colors.secondary2color");
  box-shadow: -20px 0px 0px #ffc900;
}
.bq-short {
  border-left: 10px solid theme("colors.secondary2color");
}
.bq-medium {
  box-shadow: -20px 0px 0px grey;
  border-left: 10px solid #ffc900;
}
blockquote {
  text-indent: 0;
  hanging-punctuation: first;
}
.bq-short::before {
  content: open-quote;
  color: theme("colors.secondary2color");
}
.bq-short::after {
  content: close-quote;
  color: grey;
}

.bq-long::before {
  content: open-quote;
  color: grey;
}
.bq-long::after {
  content: close-quote;
  color: #ffc900;
}
.bq-medium::before {
  content: open-quote;
  color: theme("colors.secondary2color");
}
.bq-medium::after {
  content: close-quote;
  color: grey;
}
figcaption {
  font-weight: bold;
  color: theme("colors.primarycolor");
  text-align: center;
}
.main-container > .a {
  text-decoration: none;
  color: #ffc900;
  border-bottom: 4px solid grey;
  font-weight: bold;
}
.main-container > .a:hover {
  background: grey;
  border-bottom: 4px solid theme("colors.secondary2color");
}
@media screen and (max-width: 650px) {
  body {
    max-width: 121rem;
    padding: 5rem;
    margin: 5rem auto;
    
  }
  .list a {
    font-size: 2rem;
  }
  .responsive-text {
    font-size: smaller; /* Adjust as needed */
  }
  :root {
    --heading-text: 1.8vh;
  }
}

@media screen and (min-width: 768px) {
  body {
    max-width: 121rem;
    padding: 5rem;
    margin: 5rem auto;
  }
  .headline {
    font-size: 12.4rem;
  }
  .subhead {
    font-size: 3.6rem;
  }
  .tag {
    margin-right: 2rem;
    font-size: 1.6rem;
  }
  aside {
    font-size: 3.6rem;
  }
  .article-para-1 {
    font-size: 2.4rem;
  }
  :root {
    --heading-text: 2.2vh;
  }
}
@media screen and (min-width: 1024px) {
  body {
    max-width: 117rem;
    padding: 5rem;
    margin: 5rem auto;
  }
  .article-meta {
    flex-direction: row;
    align-items: center;
    padding: 5rem;
    margin: 5rem auto;
  }
  .author {
    margin-bottom: unset;
    padding: 5rem;
    margin: 5rem auto;
  }
  :root {
    --heading-text: 3vh;
  }
}
@media screen and (min-width: 1200px) {
  body {
    max-width: 125rem;
    padding: 5rem;
    margin: 5rem auto;
  }
  :root {
    --heading-text: 3.5vh;
  }
}

